<template>
    <div v-if="readyState">
        <NavII @menuStateChange="menuStatus=!menuStatus"></NavII>
        <ArticleTiledBackground></ArticleTiledBackground>
        <div class="content">
            <transition name="bg" appear>
                <ArticlePhoto :menu-status="menuStatus" :scroll-change="scrollChange"
                              :photo-url="messageIllustration"></ArticlePhoto>
            </transition>
            <transition name="messageIllustrationInfo" appear>
                <div class="fix-div">
                    <div :class="[{'yuki-messageBoard-illustration-change':menuStatus || !scrollChange}]"
                         class="yuki-messageBoard-illustration-title">
                        <span @click.prevent="goIllustrationPage"
                              id="yuki-messageBoard-illustration-title">ほぼゆかれいむ</span>
                    </div>
                    <div :class="[{'yuki-messageBoard-illustration-change':menuStatus || !scrollChange}]"
                         class="yuki-messageBoard-illustration-artist">
    <span id="yuki-messageBoard-illustration-artist" @click.prevent="goIllustratorPage">Illustrator :
    <span id="yuki-messageBoard-illustration-artist-name">shnva</span></span>
                    </div>
                </div>
            </transition>
            <div class="title selectNG">
                <div class="title-position">
                    <transition name="title" appear>
                        <div>
                            <div style="transition: 0.6s ease-in-out"
                                 :class="[{'yuki-messageBoard-title-change':!scrollChange}]">
                                <span id="messageTitle">掲示板</span>
                            </div>
                        </div>
                    </transition>
                </div>
                <transition name="titleInfo" appear>
                    <div class="messageTitle-info">
                        <div style="transition: 0.6s ease-in-out"
                             :class="[{'yuki-messageBoard-titleInfo-change':!scrollChange}]">
                            <span>Message Board</span>
                        </div>
                    </div>
                </transition>
            </div>
            <transition name="normal" appear>
                <div>
                    <div class="messageCenter-part">
                        <div class="messagePoetry selectNG">
                            <span>少年易学老难成，一寸光阴不可轻。<br>未觉池塘春草梦，阶前梧叶已秋声。<br><span
                                    style="font-size: 15px">【南宋】朱熹《偶成》</span></span>
                        </div>
                        <!--                        <ArticleComment :comment-list="messageBoard.commentList"></ArticleComment>-->
                        <CommentII :comment-collection="messageBoardComment"
                                   @replay="changeReceiver(arguments)"></CommentII>
                        <TextArea :father-id="commentFatherId" :is-father="isFromFather" :addressee="receiver"
                                  superior-id="2" @commentUpdate="updateComment(arguments)"
                                  @receiverClear="clearReceiver"></TextArea>
                    </div>
                    <Footer :no-line="true"></Footer>
                </div>
            </transition>
        </div>
        <div class="flandre-position" @click="returnTop" :class="[{'flandre-disappear':menuStatus || scrollChange}]">
            <img @mousedown.prevent="()=>{return false;}" class="flandre-img"
                 src="https://cdn.jsdelivr.net/gh/yukibako/ImgHosting/Illustration/flandre.png">
        </div>
    </div>
</template>

<script>
    import ArticleTiledBackground from "../components/Article/ArticleTiledBackground";
    import ArticlePhoto from "../components/Article/ArticlePhoto";
    import ArticleComment from "../components/Article/ArticleComment";
    import TextArea from "../components/Comment/TextArea";
    import Footer from "../components/Footer";
    import NavII from "../components/NavII";
    import CommentII from "../components/CommentII/CommentII";

    export default {
        name: "MessageBoard",
        components: {CommentII, NavII, Footer, TextArea, ArticleComment, ArticlePhoto, ArticleTiledBackground},
        data: function () {
            return {
                readyState: false,
                hasError: false,
                scrollChange: true,
                menuStatus: false,
                messageIllustration: 'https://cdn.jsdelivr.net/gh/yukibako/ImgHosting/Illustration/74143217_p0.png',
                messageBoardComment: {},
                //评论控制相关变量
                isFromFather: null,
                receiver: null,
                commentFatherId: 0,
            }
        },
        methods: {
            goIllustrationPage() {
                window.open('https://www.pixiv.net/artworks/74143217', '_blank');
            },
            goIllustratorPage() {
                window.open('https://www.pixiv.net/users/12296581', '_blank');
            },
            scrollJudge() {
                let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
                this.scrollChange = scrollTop < window.screen.availHeight * 0.3;
            },
            returnTop() {
                window.scroll({top: 0, left: 0, behavior: 'smooth'});
            },
            illustrationLoad(key) {
                let newImg = new Image();
                let f = () => {
                    newImg.src = this.messageIllustration;
                    newImg.onload = () => {
                        this.$emit('hasLoaded');
                        this.readyState = true;
                    };
                    //加载错误时的情况
                    newImg.onerror = () => {
                        this.hasError = true;
                        this.$emit('Error');
                    };
                };
                if (key)
                    setTimeout(() => f(), 2100);
                else
                    f();
            },
            changeReceiver(result) {
                let y = document.documentElement.scrollHeight || document.body.scrollHeight;
                let x = document.documentElement.scrollWidth || document.body.scrollWidth;
                this.receiver = result[0];
                this.commentFatherId = result[1];
                this.isFromFather = result[2];
                if (x > 330 && x < 400) {
                    window.scroll({top: y - 1030, left: 0, behavior: 'smooth'});
                } else
                    window.scroll({top: y, left: 0, behavior: 'smooth'});
            },
            clearReceiver() {
                this.receiver = null;
                this.commentFatherId = 0;
            },
            updateComment(result) {
                //评论全换
                if (result[1] == 201) {
                    this.messageBoardComment.commentList = result[0];
                }
                //只换单个评论
                else {
                    for (let i = 0; i < this.messageBoardComment.commentList.length; i++) {
                        if (this.messageBoardComment.commentList[i].commentId === result[0].commentId) {
                            this.messageBoardComment.commentList[i].childComments = result[0].childComments;
                            break;
                        }
                    }
                }
            }
        },
        created() {
            axios.get('http://47.106.13.19:8848/messageBoard').then((res) => {
                this.messageBoardComment = res.data.data;
            });
            window.addEventListener('scroll', () => this.scrollJudge());
            document.title='掲示板 | YUKIBAKO';
        },
        mounted() {
            if (!this.$store.state.hadEntered)
                window.onload = () => {
                    this.illustrationLoad(false);
                    this.$store.state.hadEntered = true;
                };
            else
                this.illustrationLoad(true);
            console.log(this.messageBoardComment.commentList);
        },
        destroyed() {
            this.$emit('viewChange');
        },
    }
</script>


<style scoped src="../assets/css/yuki-animation.css"></style>
<style scoped src="../assets/css/article/yuki-article.css"></style>
<style scoped>
    .messageIllustrationInfo-enter,
    .messageIllustrationInfo-leave-to {
        opacity: 0;
        filter: blur(2px);
    }

    .messageIllustrationInfo-enter-active,
    .messageIllustrationInfo-leave-active {
        transition: 1.2s ease-in-out;
        transition-delay: 2.3s;
    }

    .yuki-messageBoard-illustration-change {
        opacity: 0;
    }

    .yuki-messageBoard-title-change {
        opacity: 0;
        filter: blur(3px);
        transform: translateY(60px);
    }

    .yuki-messageBoard-titleInfo-change {
        opacity: 0;
        filter: blur(3px);
        transform: translateX(-30px);
    }

    .yuki-messageBoard-illustration-title {
        transition: 0.6s ease-in-out;
        position: absolute;
        right: 2.5vw;
        bottom: 10%;
        user-select: none;
        -moz-user-select: none;
        -webkit-user-select: none;
    }

    #yuki-messageBoard-illustration-title {
        font-family: "DFM";
        font-size: 23px;
        letter-spacing: 2px;
        transition: 0.45s ease-in-out;
        font-weight: 600;
        text-decoration: none;
        z-index: 80;
        color: #fffee1;
        text-shadow: 4px 4px 15px #ff3a36;
    }

    #yuki-messageBoard-illustration-title:hover {
        color: #ffcecd;
        text-shadow: 4px 4px 15px #66ccff;
    }

    .yuki-messageBoard-illustration-artist {
        color: #fffee1;
        text-shadow: 4px 4px 15px #ff3a36;
        position: absolute;
        right: 1vw;
        bottom: 4%;
        transition: 0.6s ease-in-out;
        user-select: none;
        -moz-user-select: none;
        -webkit-user-select: none;
    }

    #yuki-messageBoard-illustration-artist {
        font-family: "Lucida Handwriting";
        font-size: 19px;
        letter-spacing: 2px;
        font-weight: 700;
        transition: 0.45s ease-in-out;
    }

    #yuki-messageBoard-illustration-artist:hover {
        color: #ffcecd;
        text-shadow: 4px 4px 15px #66ccff;
    }

    #yuki-messageBoard-illustration-artist-name {

    }


    #messageTitle {
        color: #e8c3ff;
        text-shadow: 4px 4px 10px rgb(155, 230, 255);
        letter-spacing: 18px;
        font-size: 45px;
    }

    .messageTitle-info {
        position: absolute;
        top: calc(93vh - 100px);
        left: 0;
        width: 100%;
        color: #f7bac5;
        text-shadow: 4px 4px 10px rgb(255, 253, 160);
        font-size: 16px;
        letter-spacing: 2px;
        text-align: center;
        line-height: 25px;
        font-family: "Lucida Handwriting";
    }

    .messageCenter-part {
        width: 100%;
        max-width: 950px;
        margin: calc(100vh - 100px) auto 0 auto;
    }

    .messagePoetry {
        color: #d9a3ad;
        text-shadow: 4px 4px 10px rgb(255, 253, 160);
        text-align: center;
        font-size: 20px;
        line-height: 40px;
        letter-spacing: 5px;
        padding: 130px 0 50px 0;
    }

    .fix-div {
        position: absolute;
        top: 0;
        right: 0;
        height: calc(100vh - 100px);
        width: 50vw;
    }

    @media only screen and (min-width: 1201px) {
        .fix-div {
            height: 100vh;
        }

        .messageTitle-info {
            top: 79.5%;
        }

        .messageCenter-part {
            margin-top: 100vh;
        }
    }

    @media only screen and ( max-width: 768px) {
        .fix-div {
            position: relative;
            height: 56.3vw;
            width: 100vw;
        }

        .title {
            line-height: 45px;
            position: relative;
            top: 0;
        }

        .title-position {
            min-height: 0;
        }

        .messageTitle-info {
            position: relative;
            top: 0;
            padding-right: 10px;
            margin-top: 0;
            text-align: right;
        }

        .messageCenter-part {
            margin-top: 0;
        }

        #yuki-messageBoard-illustration-title {
            font-size: 18px;
            letter-spacing: 2px;
            font-weight: 600;
        }

        #yuki-messageBoard-illustration-artist {
            font-size: 16px;
            letter-spacing: 1px;
        }

        .yuki-messageBoard-illustration-title {
            right: 28px;
            bottom: 40px;
        }

        .yuki-messageBoard-illustration-artist {
            right: 13px;
            bottom: 8px;
        }
    }

    @media only screen and ( max-width: 500px) {
        .messagePoetry {
            font-size: 16px;
            letter-spacing: 3px;
        }
    }

    @media only screen and ( max-width: 499px) {
        #yuki-messageBoard-illustration-title {
            font-size: 14px;
            letter-spacing: 1px;
            font-weight: 600;
        }

        #yuki-messageBoard-illustration-artist {
            font-size: 13px;
            letter-spacing: 0;
        }

        .yuki-messageBoard-illustration-title {
            right: 18px;
            bottom: 26px;
        }

        .yuki-messageBoard-illustration-artist {
            right: 2px;
            bottom: 5px;
        }
    }
</style>
